code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
    margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #212121;
  padding-top: 50px;
  color: white;
  text-align: center;
}


.Cell {
  height: 12vh;
  width: 12vh;
  border-radius: 1px;
  background-color: #263238;
  -webkit-transition: background-color 0.7s ease;
  transition: background-color 0.7s ease;
}

.Cell-lit {
  background-color: #00BCD4;
}


@media only screen and (max-width: 1024px) {
  .Cell {
    height: 10vh;
    width: 10vh;
  }
}

@media only screen and (max-width: 420px) {
  .Cell {
    height: 8vh;
    width: 8vh;
  }
}


@media only screen and (max-width: 375px) {
  .Cell {
    height: 9vh;
    width: 9vh;
  }
}
.Board {
  margin: 0 auto;
  border-radius: 10px;
  /* box-shadow: 10px 10px 0 red; */
  box-shadow: 0 0 50px #fff, -10px 0 80px #f0f, 10px 0 80px #0ff;
  cursor: pointer;
}

table tr:first-child td:first-child {
  border-radius: 10px 0 0 0 !important;
}

table tr:first-child td:last-child {
  border-radius: 0 10px 0 0 !important;
}

table tr:last-child td:first-child {
  border-radius: 0 0 0 10px !important;
}

table tr:last-child td:last-child {
  border-radius: 0 0 10px 0 !important;
}

@font-face {
  font-family: neon;
  src: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/707108/neon.ttf);
}

.Board-title {
  display: flex;
  /* flex-direction: column; */
  align-items: center;
  justify-content: center;
  margin-bottom: 3vh;
}

.neon-orange {
  font-family: neon;
  color: #fb4264;
  font-size: 10vh;
  line-height: 9vw;
  text-shadow: 0 0 3vw #f40a35;
  animation: neon-orange 1s ease infinite;
  -moz-animation: neon-orange 1s ease infinite;
  -webkit-animation: neon-orange 1s ease infinite;
}

.neon-blue {
  font-family: neon;
  color: #426dfb;
  font-size: 10vh;
  line-height: 9vw;
  text-shadow: 0 0 3vw #2356ff;
  animation: neon-blue 2s linear infinite;
  -moz-animation: neon-blue 2s linear infinite;
  -webkit-animation: neon-blue 2s linear infinite;
  -o-animation: neon-blue 2s linear infinite;
}

.winner .neon-blue,
.winner .neon-orange {
  font-size: 10vh;
}

@-webkit-keyframes neon-orange {

  0%,
  100% {
    text-shadow: 0 0 1vw #fa1c16, 0 0 3vw #fa1c16, 0 0 10vw #fa1c16,
      0 0 10vw #fa1c16, 0 0 0.4vw #fed128, 0.5vw 0.5vw 0.1vw #806914;
    color: #fed128;
  }

  50% {
    text-shadow: 0 0 0.5vw #800e0b, 0 0 1.5vw #800e0b, 0 0 5vw #800e0b,
      0 0 5vw #800e0b, 0 0 0.2vw #800e0b, 0.5vw 0.5vw 0.1vw #40340a;
    color: #806914;
  }
}

@keyframes neon-orange {

  0%,
  100% {
    text-shadow: 0 0 1vw #fa1c16, 0 0 3vw #fa1c16, 0 0 10vw #fa1c16,
      0 0 10vw #fa1c16, 0 0 0.4vw #fed128, 0.5vw 0.5vw 0.1vw #806914;
    color: #fed128;
  }

  50% {
    text-shadow: 0 0 0.5vw #800e0b, 0 0 1.5vw #800e0b, 0 0 5vw #800e0b,
      0 0 5vw #800e0b, 0 0 0.2vw #800e0b, 0.5vw 0.5vw 0.1vw #40340a;
    color: #806914;
  }
}

@-webkit-keyframes neon-blue {

  0%,
  100% {
    text-shadow: 0 0 1vw #1041ff, 0 0 3vw #1041ff, 0 0 10vw #1041ff,
      0 0 10vw #1041ff, 0 0 0.4vw #8bfdfe, 0.5vw 0.5vw 0.1vw #147280;
    color: #28d7fe;
  }

  50% {
    text-shadow: 0 0 0.5vw #082180, 0 0 1.5vw #082180, 0 0 5vw #082180,
      0 0 5vw #082180, 0 0 0.2vw #082180, 0.5vw 0.5vw 0.1vw #0a3940;
    color: #146c80;
  }
}

@keyframes neon-blue {

  0%,
  100% {
    text-shadow: 0 0 1vw #1041ff, 0 0 3vw #1041ff, 0 0 10vw #1041ff,
      0 0 10vw #1041ff, 0 0 0.4vw #8bfdfe, 0.5vw 0.5vw 0.1vw #147280;
    color: #28d7fe;
  }

  50% {
    text-shadow: 0 0 0.5vw #082180, 0 0 1.5vw #082180, 0 0 5vw #082180,
      0 0 5vw #082180, 0 0 0.2vw #082180, 0.5vw 0.5vw 0.1vw #0a3940;
    color: #146c80;
  }
}

.Board-button {
  display: flex;
  align-content: center;
  justify-content: center;
  
  margin-top: 5vh;
}

button {
  border: none;
  font-family: inherit;
  font-size: 1.4rem;
  cursor: pointer;
  width: auto;
  margin: 5px 2px 2px 2px;
  letter-spacing: 1px;
  outline: none;
  background: #00bcc4;
  color: #fff;
  box-shadow: 0 6px #0097a7;
  border-radius: 5px;
  padding-top: 4px;
}

button:hover {
  background-color: #0097a7;
  box-shadow: 0 6px #096670;
}

button:active {
  box-shadow: 0 6px #0097a7;
  background-color: #3f71aa;
  -webkit-transform: translateY(4px);
          transform: translateY(4px);
}

.Board-button-won {
  display: flex;
  align-content: center;
  justify-content: center;
  margin-top: 100px;
}

.Board-slider {
  width: 30%;
}

.Board-slider-input {
  -webkit-appearance: none;  /*Override default CSS styles */
  -moz-appearance: none;
       appearance: none;
  width: 100%; /* Full-width */
  height: 25px; /* Specified height */
  background: #00bcc4; /* Grey background */
  outline: none; /* Remove outline */
  /* opacity: 0.7; Set transparency (for mouse-over effects on hover) */
  -webkit-transition: .2s; /* 0.2 seconds transition on hover */
  -webkit-transition: opacity .2s;
  transition: opacity .2s;
  margin: 13px 10px 10px 0px
}

.Board-slider-input {
  cursor: pointer;
}

.Board-slider:hover {
  opacity: 0.8; /* Fully shown on mouse-over */
}

.child {
  width: 15%;
}

p {
  font-size: 1.6vh;
  margin-top: 20%;
}

@media only screen and (max-width: 1024px) {
